/* src/styles/Header.css */

header {
    background-color: #1a1a1a;
    color: #fff;
    padding: 0.1rem;
    
  }
  
  nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  nav img {
    width: 200px;
    height: 100px; /* Adjust the logo size */
    border-radius: 8px; /* Rounded corners for a modern look */
    border: 3px solid #ffffff; /* Highlight border color */
    box-shadow: 0 0 10px rgba(252, 252, 252, 0.5); /* Subtle glow effect */
    transition: box-shadow 0.3s ease-in-out, transform 0.3s ease-in-out; /* Smooth transition for effects */
  }
  
  nav img:hover {
    box-shadow: 0 0 15px rgba(0, 123, 255, 0.8); /* Increased glow on hover */
    transform: scale(1.05); /* Slightly enlarge on hover */
  }
  
  
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
  }
  
  ul li {
    margin: 0 1rem;
    position: relative; /* Required for positioning the ::after element */
  }
  
  ul li a {
    color: #fff;
    text-decoration: none;
    font-size: 1rem;
    padding: 0.5rem 1rem;
    transition: background-color 0.3s, color 0.3s;
  }
  
  ul li a:hover {
    background-color: #555;
    color: rgb(255, 255, 255); /* Change to any color you like for hover effect */
  }
  
 
  
  ul li:hover::after {
    width: 100%;
  }
  
  
  /* Responsive Styles */
  
  @media (max-width: 768px) {
    nav {
      flex-direction: column;
      align-items: flex-start;
    }
  
    ul {
      flex-direction: column;
      width: 100%;
    }
  
    ul li {
      margin: 0.5rem 0;
    }
  
    nav img {
      width: 50px;
      height: 50px; /* Adjust logo size for smaller screens */
    }
  }
  
  @media (max-width: 480px) {
    ul li a {
      font-size: 0.9rem; /* Smaller font size for very small screens */
      padding: 0.4rem 0.8rem;
    }
  
    nav img {
      width: 100px; /* Further adjust logo size for very small screens */
    }
  }
  